import app from '@/createEntrypoint'
import { createRouter, createWebHashHistory } from 'vue-router'

import Home from '@/views/Home'

const router = createRouter({
    routes: [
        {
            path: '/',
            component: Home,
            name: 'Home'
        },
        {
            path: '/Home',
            component: Home,
            name: 'Home'
        }
    ],
    history: createWebHashHistory()
})

app.use(router)
